<template>
    <div class="message-input">
        <div class="message-box">
            <div class="message-user">
                <HeadIcon color="#ef6d38" :width="80" />
                <div>
                    <p class="name">William</p>
                </div>
            </div>
            <div class="message-content">
                <p class="title" v-if="!notitle"><el-input v-model="input" placeholder="请输入標題"></el-input></p>
                <div class="text">
                    <el-input
                        type="textarea"
                        :rows="2"
                        placeholder="请输入内容"
                        :autosize="{ minRows: 4, maxRows: 10}"
                        v-model="textarea">
                    </el-input>
                </div>
            </div>
        </div>
        <div class="car-btn">提出討論／詢問</div>
    </div>

</template>

<script>
import HeadIcon from '../../layout/headIcon.vue'
export default {
    name: 'messageInput',
    components:{
        HeadIcon,
    },
    props: {
        notitle: {
            type:Boolean,
            defalut: false,
        }
        
    },

    data () {
        return {
            input:'',
            textarea:'',

        }
    }
}
</script>

<style lang='scss' scoped>
.message-input {
    max-width: 1200px;
    margin: 30px auto;
    width: 90%;
    .message-box {
        border: 1px solid #ccc;
        border-radius: 24px;
        padding: 40px;
        position: relative;
        &:nth-child(even) {
            background: #f4f4f4;
        }
        &:hover {
            .el-icon-right {
                background:#ef6d38;
            }
        }
        .message-user {
            display: flex;
            align-items: center;
            .head-icon {
                color: #ef6d38;
                margin: 0;
                margin-right: 20px;
            }
            .name {
                color: #ef6d38;
                font-size: 22px;
                margin-bottom: 5px;
            }
        }
        .message-content {
            max-width: 950px;
            margin-left: 107px;
            width: 80%;
            .title {
                font-size: 22px;
                text-decoration: underline;
                margin-bottom: 20px;
            }
            .text {
                color: #333333;
            }
        }
    }
    @media screen and (max-width: 768px){
        .message-box .message-user .head-icon {
            width: 40px;
            height: 40px;
            font-size: 40px;
        }
        .message-box .message-content {
            margin-left: 0;
            width: 100%;
            margin-top: 15px;
        }
        
    }
}
</style>


