<template>
<div class="buycars">
    <div class="buycars-title">
            <p class="sub-title">購車需求</p>
            <p class="title">BUY CARS</p>
        </div>
    <div class="buycars-content" v-if="!Info">
        <div class="buycars-search">
            <el-input placeholder="搜尋留言" v-model="input3" class="input-with-select">
                <el-button slot="append" @click="openInfo()" icon="el-icon-search"></el-button>
            </el-input>
        </div>
        <Message @openInfo="openInfo" :items="5"/>
        
        <div class="buycars-pade">
            <el-pagination
                :page-size="20"
                :pager-count="11"
                layout="prev, pager, next"
                :total="100">
            </el-pagination>
        </div>
        <MessageInput style="margin-top: 150px;"/>
    </div>
    <div v-else class="buycars-content-info">
        <MessageInfo />
        <div class="car-btn car-btn-gray" @click="openInfo()">返回列表</div>
        <MessageInput style="margin-top: 150px;" :notitle="true"/>
    </div>
</div>
</template>

<script>
import Message from '../message'
import MessageInfo from '../message/info'
import MessageInput from '../message/input'
export default {
    components: { Message, MessageInfo , MessageInput},
    name:'buycars',
    data() {
        return {
            input3:'',
            Info:false,
        }
    },
    methods: {
        openInfo() {
            this.Info = !this.Info
        }
    }
}
</script>

<style lang="scss">
.buycars {
    padding: 105px 0;
    background: url('~@/assets/img/buycars/bg.png') no-repeat center;
    .buycars-title {
        background: url('~@/assets/img/buycars/buycars-banner.png') no-repeat center;
        background-size: cover;
    }
    .buycars-content {
        margin-top: 50px;
        .buycars-search {
            width: 90%;
            max-width: 1200px;
            margin: auto;
            .el-input__inner {
                width: 100%;
                border-radius: 50px 0 0 50px;
            }
            .el-input-group__append {
                background: #ef6d38;
                color: #fff;
                border-radius: 0 50px 50px 0;
            }
        }
        .el-breadcrumb{
            max-width: 1200px;
            margin: auto;
            margin-bottom: 100px;
        }
        
    }
    .buycars-pade {
        max-width: 1200px;
        margin: auto;
    }
    @media screen and (max-width: 768px){ 
        .car-btn-gray {
            width: 90%;
        }
    }
}

</style>


